/* src/pages/About.css */
.about {
  padding: 100px 0;
  background-color: var(--light-color);
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.about-content {
  max-width: 800px;
  margin: 0 auto;
}

.about-content h1 {
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: var(--dark-color);
  text-align: center;
}

.about-description {
  margin-bottom: 60px;
}

.about-description p {
  font-size: 1.1rem;
  line-height: 1.7;
  margin-bottom: 20px;
  color: var(--text-color);
}

/* 時間線樣式 */
.timeline-section {
  margin-top: 80px;
}

.timeline-section h2 {
  font-size: 1.8rem;
  margin-bottom: 30px;
  color: var(--dark-color);
}

.timeline-actions {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}

.timeline-btn {
  padding: 12px 20px;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.timeline-btn:hover {
  background-color: #f5f5f5;
}

.timeline-container {
  position: relative;
  margin: 50px 0;
}

.timeline {
  position: relative;
  height: 900px;
  margin: 0 auto;
}

.timeline-line {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  height: 100%;
  background-color: #ddd;
}

.timeline-year {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    width: 100%; /* 確保元素橫跨整個寬度 */
    text-align: center; /* 使內容居中 */
  }
  
  .timeline-year span {
    display: inline-block;
    background-color: #32325d;
    color: white;
    padding: 2px 8px;
    border-radius: 10px;
    font-size: 0.8rem;
    /* 刪除 transform: translateX(-50%); 因為已經用 text-align: center 居中了 */
  }

.timeline-item {
  position: absolute;
  width: 45%;
}

.timeline-item.experience {
  left: 0;
}

.timeline-item.education {
  right: 0;
}

.timeline-content {
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  height: 100%;
}

.timeline-item.experience .timeline-content {
  background-color: #b74c43; /* 紅色調 */
  color: white;
}

.timeline-item.education .timeline-content {
  background-color: #4682b4; /* 藍色調 */
  color: white;
}

.timeline-content h3 {
  font-size: 1.3rem;
  margin-bottom: 10px;
  color: inherit;
}

.timeline-content p {
  margin-bottom: 5px;
  font-size: 1rem;
}

.timeline-period {
  font-style: italic;
  opacity: 0.9;
}

.timeline-content:hover {
  transform: translateY(-5px);
}

/* 響應式設計 */
@media (max-width: 768px) {
  .timeline-item {
    width: 85%;
    left: 0 !important;
    right: auto !important;
  }
  
  .timeline-line {
    left: 20px;
  }
  
  .timeline-year {
    left: 20px;
    text-align: left;
  }
  
  .timeline-item.experience, .timeline-item.education {
    left: 40px !important;
  }
  
  .timeline-actions {
    flex-direction: column;
    gap: 10px;
  }
}