/* src/pages/Contact.css */
.contact {
    padding: 100px 0;
    background-color: var(--light-color);
  }
  
  .container {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .contact-content {
    text-align: center;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .contact-content h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: var(--dark-color);
  }
  
  .contact-content p {
    font-size: 1.2rem;
    color: var(--text-color);
    margin-bottom: 30px;
  }
  
  .social-links {
    margin-top: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .social-button {
    display: flex;
    align-items: center;
    padding: 12px 25px;
    border-radius: 50px;
    color: white;
    text-decoration: none;
    font-weight: 600;
    transition: all 0.3s ease;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .social-button i {
    font-size: 1.5rem;
    margin-right: 10px;
  }
  
  .social-button span {
    font-size: 1rem;
  }
  
  .social-button.linkedin {
    background-color: #0077b5;
  }
  
  .social-button.medium {
    background-color: #333;
  }
  
  .social-button.github {
    background-color: #333;
  }
  
  .social-button:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
  }
  
  /* Responsive design */
  @media (max-width: 768px) {
    .social-links {
      flex-direction: column;
      align-items: center;
    }
    
    .social-button {
      width: 80%;
      justify-content: center;
    }
    
    .contact {
      padding: 60px 0;
    }
    
    .contact-content h1 {
      font-size: 2rem;
    }
  }